n              for=\"username\"\r\n              class=\"block text-900 text-xl font-medium mb-2\"\r\n            >\r\n              Username\r\n            </label>\r\n            <InputText\r\n              id=\"username\"\r\n              v-model=\"form.login\"\r\n              type=\"text\"\r\n              class=\"w-full mb-3\"\r\n              style=\"padding: 1rem\"\r\n            />\r\n\r\n            <label\r\n              for=\"password\"\r\n              class=\"block text-900 font-medium text-xl mb-2\"\r\n            >\r\n              Password\r\n            </label>\r\n            <Password\r\n              id=\"password\"\r\n              v-model=\"form.password\"\r\n              :toggleMask=\"true\"\r\n              class=\"w-full mb-5\"\r\n              inputClass=\"w-full\"\r\n              :feedback=\"false\"\r\n              @keyup.enter=\"submit\"\r\n            ></Password>\r\n\r\n            <!-- <div class=\"flex align-items-center justify-content-between mb-5\">\r\n              <div class=\"flex align-items-center\">\r\n                <Checkbox\r\n                  id=\"rememberme\"\r\n                  v-model=\"checked\"\r\n                  :binary=\"true\"\r\n                  class=\"mr-2\"\r\n                ></Checkbox>\r\n                <label for=\"rememberme\">Remember me</label>\r\n              </div>\r\n              <a\r\n                :href=\"route('register')\"\r\n                class=\"font-medium no-underline ml-2 text-right cursor-pointer\"\r\n                style=\"color: var(--primary-color)\"\r\n                >Register</a\r\n              >\r\n              <a\r\n                class=\"font-medium no-underline ml-2 text-right cursor-pointer\"\r\n                style=\"color: var(--primary-color)\"\r\n                >Forgot password?</a\r\n              >\r\n            </div> -->\r\n            <Button\r\n              type=\"submit\"\r\n              @click=\"submit\"\r\n              label=\"Sign In\"\r\n              class=\"w-full p-3 text-xl\"\r\n              :loading=\"form.processing\"\r\n            ></Button>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport { Head } from '@inertiajs/vue3';\r\nimport Checkbox from 'primevue/checkbox';\r\nimport Button from 'primevue/button';\r\nimport InputText from 'primevue/inputtext';\r\nimport Password from 'primevue/password';\r\nimport Message from 'primevue/message';\r\n\r\nexport default {\r\n  components: {\r\n    Button,\r\n    Checkbox,\r\n    InputText,\r\n    Password,\r\n    Message,\r\n    Head,\r\n  },\r\n  computed: {\r\n    logoColor() {\r\n      return 'dark';\r\n    },\r\n  },\r\n  data() {\r\n    return {\r\n      form: this.$inertia.form({\r\n        login: '',\r\n        password: '',\r\n        // remember: false,\r\n      }),\r\n    };\r\n  },\r\n  methods: {\r\n    submit() {\r\n      this.form\r\n        // .transform((data) => ({\r\n        //   ...data,\r\n        //   remember: this.form.remember ? 'on' : '',\r\n        // }))\r\n        .post(this.route('login'), {\r\n          onFinish: () => this.form.reset('password'),\r\n        });\r\n      //   console.log(this.form);\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.pi-eye {\r\n  transform: scale(1.6);\r\n  margin-right: 1rem;\r\n}\r\n\r\n.pi-eye-slash {\r\n  transform: scale(1.6);\r\n  margin-right: 1rem;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
